.navigation {
    grid-area: nav;
    padding: 50px 0;
}

.navigation__link {
    display: block;
    text-decoration: none;
    color: black;
    transition: 0.1s;
    font-weight: normal;
    font-size: 15px;
    margin-bottom: 20px;
}

.navigation__link:hover {
    color: #142668;
}

.active-link {
    font-weight: bolder;
}














