@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500,600,700,800,900&display=swap);
* {
    font-family: 'Montserrat', sans-serif;
}

body {
    margin: 0;
    background-color: white;
}

.app-wrapper {
    display: grid;
    grid-template-columns: 1fr 10fr;
    grid-template-rows: 60px 1fr;
    grid-template-areas: "header header" "nav profile";
    max-width: 1200px;
    margin: 0 auto;
}

.app-wrapper-content {
    margin-top: 50px;
}

.btn {
    background-color: grey;
    border: 0;
    outline: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
}


.navigation {
    grid-area: nav;
    padding: 50px 0;
}

.navigation__link {
    display: block;
    text-decoration: none;
    color: black;
    transition: 0.1s;
    font-weight: normal;
    font-size: 15px;
    margin-bottom: 20px;
}

.navigation__link:hover {
    color: #142668;
}

.active-link {
    font-weight: bolder;
}















.dialogs {
