    height: 40px;
    border: 1px solid transparent;
    background-color: #142668;
    font-weight: bolder;
    cursor: pointer;
    color: white;
    outline: none;
}

.pagination__list-swipe-btn-prev--disable,
.pagination__list-swipe-btn-prev {
    margin-right: 15px;
}

.pagination__list-swipe-btn-prev:hover,
.pagination__list-swipe-btn-next:hover {
    color: #142668;
    background-color: white;
    border: 1px solid #142668
}

.pagination__list-swipe-btn-prev--disable,
.pagination__list-swipe-btn-next--disable {
    background-color: #DDDDDD;
}

.animation {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-height: 800px;
}

.cssload-square {
    width: 5px;
    height: 29px;
    background: #142668;
    border-radius: 10px;
    display: block;
    animation: cssload-turn 2s ease infinite;
    -o-animation: cssload-turn 2s ease infinite;
    -ms-animation: cssload-turn 2s ease infinite;
    -webkit-animation: cssload-turn 2s ease infinite;
    -moz-animation: cssload-turn 2s ease infinite;
}

.cssload-top {
    position: absolute;
    left: 40%;
    top: 50%;
    transform: rotate(90deg);
    -o-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    -webkit-transform: rotate(90deg);
    -moz-transform: rotate(90deg);
}

.cssload-bottom {
    position: absolute;
    left: 40%;
    top: 50%;
    transform: rotate(-90deg);
    -o-transform: rotate(-90deg);
    -ms-transform: rotate(-90deg);
    -webkit-transform: rotate(-90deg);
    -moz-transform: rotate(-90deg);
}

.cssload-left {
    position: absolute;
    left: 40%;
    top: 50%;
}

.cssload-right {
    position: absolute;
    left: 40%;
    top: 50%;
    transform: rotate(-180deg);
    -o-transform: rotate(-180deg);
    -ms-transform: rotate(-180deg);
    -webkit-transform: rotate(-180deg);
    -moz-transform: rotate(-180deg);
}

@keyframes cssload-turn {
    0% {
        transform: translateX(0) translateY(0) rotate(0);
    }
    50% {
        transform: translateX(400%) translateY(100%) rotate(90deg);
    }
    100% {
        transform: translateX(0) translateY(0) rotate(0);
    }
}

@-webkit-keyframes cssload-turn {
    0% {
        -webkit-transform: translateX(0) translateY(0) rotate(0);
    }
    50% {
        -webkit-transform: translateX(400%) translateY(100%) rotate(90deg);
    }
    100% {
        -webkit-transform: translateX(0) translateY(0) rotate(0);
    }
}

