    align-items: center;
    flex-direction: column;
    background-color: white;
}

.login-form__sign {
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    margin: 0 0 40px;
}

.login-form__item:nth-child(2) {
    margin-bottom: 30px;
}

.login-form__item:nth-child(3) {
    margin-bottom: 20px;
}

.login-form__item:nth-child(4) {
    margin-bottom: 30px;
}

.login-form__item-input {
    color: black;
    border: 1px solid transparent;
    width: 370px;
