}

.login-form__item-checkbox {
    display: none;
}

.login-form__item-label,
.login-form__item-label--checked {
    margin-left: -155px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    position: relative;
}

.login-form__item-label::before,
.login-form__item-label--checked::before {
    position: absolute;
    content: "";
    top: 0;
    left: -30px;
    height: 20px;
    width: 20px;
    background-color: lightgrey;
}

.login-form__item-label--checked::after {
    position: absolute;
    content: "";
    top: 4px;
    left: -26px;
    height: 12px;
    width: 12px;
    background-color: #142668;
}


.login-form__item-btn {
